const filters = {
    text: "",
    hideCompletedTodos: false
}

const getFilters = () => filters;

const setFilters = (updates = {}) => {
    if(!updates) {
        return;
    }

    if(typeof updates.text === "string") {
        filters.text = updated.text;
    }

    if(typeof updates.hideCompleteTodos === "boolean") {
        filters.hideCompletedTodos = updated.hideCompleteTodos;
    }
}

export {
    getFilters,
    setFilters
}

document.querySelector('#input-search-todo').addEventListener('input', (e) => {
    e.preventDefault();
    filters.text = e.target.value;
    renderTodos(todos, filters);
});

document.querySelector('#hide-completed-todos').addEventListener('change', (e) => {
    e.preventDefault();
    filters.hideCompletedTodos = e.target.checked;
    renderTodos(todos, filters);
});
